        justify-content: space-between;

        .regButton {
          height: 50px;
          width: 150px;
          position: relative;

          .regButtonRange {
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            height: 100%;
            width: 100%;
            background: none;
            border: none;

            .regButtonText {
              align-self: center;
              position: relative;
              padding: 5px;
              font-size: 1rem;
              color: #3a3c40;
              margin: auto;
            }

            .regLoginLink {
              display: flex;
              align-self: center;
              justify-content: center;
            }
          }

          .regButtonRange:hover {
            background: rgba(46, 46, 49, 0.1);
            box-shadow: 2px 2px var(--colour-dark-grey);
          }
        }
      }

      .errorMessage {
        height: 12px;
        font-size: 12px;
        color: red;

        padding-top: 10px;
      }

      .regLabel {
        display: flex;
        width: 70%;
        background: none;
      }

      .regInput {
        background: none;
        position: relative;
        line-height: 1.5;
        width: 70%;
        border: none;
        border-bottom: 1px solid rgba(73, 73, 77, 0.4);
      }

      .regInput:focus {
        outline: none;
      }
    }
  }
}

//właściwy formularz do oddawania rzeczy:

.formHomeHeader {
  height: $baseHeight;
  width: 100vw;
  display: flex;
  flex-direction: row;

  .leftSide {
    height: 100%;
    width: 50vw;
    background: url("../../assets/Form-Hero-Image.jpg") no-repeat bottom;
    background-size: cover;
  }

  .rightSide {
    position: relative;
    width: 50vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;

    .authSection {
      cursor: pointer;
      font-size: 18px;
      width: 50vw;
      top: 2%;
      position: absolute;
      right: 10%;
      display: flex;
      align-items: center;
      justify-content: right;

      .authButtons {
        display: flex;
        flex-direction: row;
        width: 25%;
        justify-content: right;
        height: 60px;

        .authUserWrapper {
          display: flex;
          justify-content: right;
          align-items: center;
          width: auto;

          .loggedUser {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: right;
            width: 600px;
            height: 10px;
            margin-right: 20px;
          }
        }

        .authButtonWrapper {
          height: 90%;
          width: 100%;
          padding-left: 2px;
          padding-right: 2px;

          .authButton:hover {
            cursor: pointer;
            border-color: var(--colour-yellow);
            border-width: 1px;
            border-style: solid;
            box-shadow: 1px 1px var(--colour-yellow);
          }

          .authButton {
            border: none;
            background: none;
            min-width: 190px;
            height: 100%;
          }
        }
      }
    }

    .navigation {
      font-size: 18px;
      width: 50vw;
      top: 9%;
      position: absolute;
      right: 10%;
      display: flex;
      align-items: center;
      justify-content: right;

      .navButtons {
        display: flex;
        flex-direction: row;
        width: auto;
        justify-content: right;
        padding-left: 10px;
        height: 60px;

        .navButtonWrapper {
          height: 90%;
          width: 90%;
          padding-left: 2px;
          padding-right: 2px;
        }

        .navButton {
          border: none;
          background: none;
          min-width: 200px;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        .navButtonLink {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 100%;
          width: 100%;
        }

        .navButton:hover {
          cursor: pointer;
          border-color: var(--colour-yellow);
          border-width: 1px;
          border-style: solid;
          box-shadow: 1px 1px var(--colour-yellow);
        }
      }
    }

    .basePageMiddleSection {
      position: absolute;
      text-align: center;
      height: 100%;
      width: 100%;
      top: 40vh;
      display: flex;
      flex-direction: column;
      align-items: center;

      .baseText {
        width: 100%;
        font-size: 2.4rem;
        margin-bottom: 20px;
      }

      .basePageDecoration {
        width: 30%;
        height: 3.5%;
        background: url("../../assets/Decoration.svg") no-repeat center;
        background-size: cover;
        margin-bottom: 40px;
      }

      .squares {
        display: flex;
        flex-direction: row;
        margin: 30px;

        .square {
          position: relative;
          border: 1px solid black;
          height: 100px;
          width: 100px;
          padding: 20px;
          margin: 10px;
          transform: rotate(45deg);
          display: flex;
          align-items: center;
          justify-content: space-between;
          flex-direction: column;

          .innerSquare {
            transform: rotate(-45deg);
            height: 100px;
            width: 100px;

            .squareNumbers {
              font-size: 30px;
              text-align: center;
              margin: 0;
            }

            .squareText {
              font-size: 17px;
              text-align: center;
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
}

