  }

  .radioMenuWrapper {
    padding-top: 70px;
    padding-left: 150px;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    .radioMenuTitle {
      font-size: 2rem;
      margin-bottom: 2rem;
    }

    .radioMenu {
      display: flex;
      flex-direction: column;

      .radioMenuItem {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 60px;
        margin: 15px 0 15px 0;

        input[type="radio"] {
          width: 60px;
          height: 60px;
          -webkit-appearance: none;
          -moz-appearance: none;
          border: 1px solid black;
          margin-right: 40px;
        }

        input[type="radio"]:checked {
          background-color: var(--colour-yellow);
        }

        input[type="radio"]:checked:after {
          margin-left: 4.3mm;
          margin-top: -0.4mm;
          width: 3mm;
          height: 10mm;
          border: solid white;
          border-width: 0 2mm 2mm 0;
